<script>
export default {
data() {
  return {
    num:5,
    isShowStart:true,
    infoShow:false,
    index:0,
    timer:null,
    timer2:null,
    n:5,
    alertShow:false,
    inoList:[
      {imgurl:'images/gold.png',
      name:'黄金卡',
      xnum:6
      },
      {imgurl:'images/wanzi.png',
      name:'丸子',
      xnum:5
      },
      {imgurl:'images/rocket.png',
      name:'火箭',
      xnum:4
      },
      {imgurl:'images/dan.png',
      name:'弹夹',
      xnum:2
      },
      {imgurl:'images/wan.png',
      name:'豆腐丸',
      xnum:9
      },
      {imgurl:'images/feichuan.png',
      name:'飞船',
      xnum:3
      },
      {imgurl:'images/plane.png',
      name:'飞机',
      xnum:6
      },
      {imgurl:'images/superocket.png',
      name:'超级火箭',
      xnum:6
      },
      {imgurl:'images/yao.png',
      name:'止痛药',
      xnum:6
      },
      {imgurl:'images/geili.png',
      name:'给力',
      xnum:3
      },
      {imgurl:'images/feichuan.png',
      name:'飞船',
      xnum:1
      },
      {imgurl:'images/wanzi.png',
      name:'豆腐丸',
      xnum:7
      },
      {imgurl:'images/plane.png',
      name:'飞机',
      xnum:9
      },
      {imgurl:'images/dan.png',
      name:'弹夹',
      xnum:1
      },
      {imgurl:'images/rocket.png',
      name:'火箭',
      xnum:5
      },
      {imgurl:'images/gold.png',
      name:'黄金卡',
      xnum:6
      },
      {imgurl:'images/yao.png',
      name:'止痛药',
      xnum:6
      },
      {imgurl:'images/geili.png',
      name:'给力',
      xnum:8
      }
    ]
  }
},
methods:{
  startChou(){
    // console.log(this.$refs.inner.children);
    if(this.num>0){
        this.num--
    this.isShowStart =false
    // 生成0-17的随机数
    let number = Math.round(Math.random()*17)

    let count =-1
   this.timer = setInterval(() => {
     if(this.n===0){
       clearInterval(this.timer)
      
       this.index =0
      this.timer2= setInterval(()=>{
         this.index++
         if(this.index===number){
           clearInterval(this.timer2)
           
           this.index=number
           this.infoShow =true
         }
       },300)
       
     }
     count++
      this.index++
      if(count===18||this.index===18){
        count = 0
        this.index=0
        this.n--
      }
      
    }, 50);
    }else{
      this.alertShow=true
      setTimeout(() => {
        this.alertShow=false
      }, 3000);
    }
  
     
  },
  infoClick(){
    this.infoShow =false
    this.isShowStart=true
    this.n=5
    this.timer2=null
    this.timer=null
  }
 
}
}
</script>

